            <div v-if="row.sdk === '1'">
              <el-button
                type="text"
                :disabled="row.strategyId <= 0"
                @click="changeNet(row)"
                >{{ $t('cardMag.switchingNetwork') }}</el-button
              >
            </div>
            <div v-if="row.sdk === '1'">
              <el-button
                type="text"
                :disabled="row.strategyId <= 0"
                @click="changeTactics(row)"
                >{{ $t('cardMag.switchingStrategy') }}</el-button
