<template>
  <div>
    <el-dialog
      :title="$t('cardMag.switchingNetwork')"
      :visible.sync="v"
      :before-close="handleClose"
      width="480px"
      :close-on-click-modal="false"
      append-to-body
      @open="open"
    >
      <el-form
        ref="paramFormRef"
        :model="paramForm"
        :rules="paramRules"
        label-width="100px"
        size="small"
        class="demo-ruleForm"
      >
        <el-form-item :label="$t('cardMag.fullMsisdn')">
          <el-input v-model="paramForm.fullMsisdn" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item :label="$t('cardMag.netIspName')">
          <el-input v-model="paramForm.netIspName" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item :label="$t('cardMag.targetCarrier')" prop="newIspId">
          <el-select v-model="paramForm.newIspId" placeholder="请选择">
            <el-option
              v-for="item in ispIdOptions"
              :key="item.code"
              :label="item.desc"
              :value="item.code"
            >
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" size="small" :loading="btnLoading" @click="clickOk">{{
          $t('cardMag.ok')
        }}</el-button>
        <el-button size="small" @click="cancel">{{ $t('cardMag.cancel') }}</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'ChangePackageDialog',
  props: {
    visible: {
      type: Boolean,
      default: false,
    },
    modifyTitle: {
      type: String,
      default: '切网',
    },
    detailInfo: {
      type: Object,
      default: () => {},
    },
  },
  data() {
    return {
      paramForm: {
        fullMsisdn: '',
        netIspName: '',
        newIspId: '',
      },
      paramRules: {
        newIspId: [{ required: true, message: '请选择目标运营商', trigger: 'change' }],
      },
      allIspOptions: [],
      ispIdOptions: [],
      btnLoading: false,
    }
  },
  computed: {
    v: {
      get: function () {
        return this.visible
      },
      set: function (value) {
        this.$emit('update:visible', value)
      },
    },
  },
  created() {
    // this.queryFullOfferGeneralParam()
  },
  methods: {
    open() {
      for (const key in this.paramForm) {
        this.paramForm[key] = this.detailInfo[key]
      }
      this.paramForm.newIspId = ''
      this.ispIdOptions =
        this.allIspOptions?.filter((item) => item.code != this.detailInfo.netIspId) || []
    },
    queryFullOfferGeneralParam() {
      try {
        this.$api.fullMealMgr.queryFullOfferGeneralParam().then((res) => {
          this.allIspOptions = res?.Isp
        })
      } catch (e) {}
    },
    clickOk() {
      this.$refs.paramFormRef.validate((valid) => {
        if (!valid) return
        this.changeFullNetwork()
      })
    },
    changeFullNetwork() {
      this.btnLoading = true
      try {
        this.$api.fullCardMag
          .changeFullNetwork({
            fullMsisdn: this.paramForm.fullMsisdn,
            newIspId: this.paramForm.newIspId,
          })
          .then((res) => {
            this.$message.success(`切网成功！`)
            this.v = false
            this.$emit('queryFullSimCardListForPage', false)
          })
          .finally(() => {
            this.btnLoading = false
          })
      } catch (e) {
        this.btnLoading = false
      }
    },
    cancel() {
      this.v = false
    },
    handleClose() {
      this.cancel()
    },
  },
}
</script>
<style scoped>
.demo-ruleForm /deep/ .el-form-item {
  display: flex !important;
  margin: 20px 0 !important;
}
.demo-ruleForm /deep/ .el-form-item__content {
  margin-left: 0px !important;
}
.demo-ruleForm /deep/ .el-input {
  width: 300px !important;
}
</style>
